@tailwind base;
@tailwind components;
@tailwind utilities;



*{
    margin: 0; padding: 0; box-sizing: border-box;
    /* font-family: 'Poppins', sans-serif; */
    /* font-family: 'Wallpoet', sans-serif; */
    font-family: 'Inconsolata', monospace;
}
@layer components {
   .wrapper { @apply container mx-auto px-3 sm:px-4 md:px-5 lg:px-8 xl:px-10; }
   .site-btn {  @apply flex align-middle gap-2 py-1 md:py-2 px-3 md:px-4 lg:px-5 font-medium rounded-full bg-yellow-300 text-black text-base md:text-lg capitalize hover:bg-yellow-100  transition-all; }
 }

 #root{scroll-snap-type: y mandatory;}

body{background-color: rgb(30, 30, 30); color: #fff;}
.date{border: 2px solid red; background-color: #fff;border-radius: 5px; text-align: center; padding: 5px 10px;}
.expense-item{display: flex; width: fit-content; margin:10px auto; gap: 10px; flex-wrap: wrap; align-items: center; background-color: #eee; border: 2px solid green; padding:5px 10px; border-radius: 5px;}

.desktopmenu ul{display: flex; flex-wrap: wrap; gap:30px; align-items: center;}
.desktopmenu ul li a{font-size: 18px;}
.desktopmenu ul li a:hover{color: #f6e249;}

.logo{max-width:100px; position: relative;}
.mb_child_20 >*{margin-bottom: 20px;}
.mb_child_20 >*:last-child{margin-bottom: 0px;} 

.section_padding{padding-block: 60px;}
 p{font-size: 18px;}
 .logo_inr{background-color: rgb(30, 30, 30); border-radius: 50%; width: 100%;  transition: 0.3s; padding: 10px; aspect-ratio: 1;}
 header{position: sticky; width: 100%; top: 0; left: 0; z-index: 50;}
header.scrolled .logo_inr{/*width: 100px; position: absolute; left: 0; bottom: -70px;*/ margin-bottom: -50px; position: relative; box-shadow: 0px 10px 8px 0px #00000050;}
/* header.scrolled .logo_inr::after{content: ""; left: 0; bottom: 0px; position: absolute; width: 100%; height: 100%; clip-path: circle(50% at 50% 0); background-color: red;} */
header.scrolled{box-shadow: 0px 5px 20px 0px #000000;}
 .skillsection_main{width: fit-content; margin: auto;max-width: 700px;}
 .skill_section{display: flex; flex-wrap: wrap; align-items: center; padding: 20px;}
 .skillimg{width:80px;}
 .skilltxt{width: calc(100% - 80px); padding-left: 20px;}

 .custom_row{display: flex; flex-wrap: wrap; margin: 0px -15px  ;}
 .custom_row .col-3{width: 33.33%; padding: 15px;}
 .custom_row .col-4{width: 25%; padding: 15px;}

 .slider_col{padding:15px;}
 .slick-dots {width: fit-content; margin:30px auto 0px;}
 .slick-dots li{ display: inline-block; vertical-align: middle; margin-right: 10px;}
 .slick-dots li:last-child{margin-right: 0px;}
 .slick-dots li button{font-size: 0px; width: 12px; height: 12px; background-color: grey; border-radius: 50px; cursor: pointer; transition: 0.2s;}
 .slick-dots li button:hover{background-color: red;}
 .slick-dots li.slick-active button{background-color: red; }

 .slider_col .webimg{position: relative; border-radius: 8px; overflow: hidden; background-color: white; height: 400px;}
 .slider_col .knowmore{position: absolute; width: 100%; height: 100%; left: 0; top:100%; transition: 0.3s; background-color: #0074a180; display: flex; justify-content: center; align-items: center;}
 .slider_col .webimg:hover .knowmore{top: 0;}
 .slider_col .webimg img{width: 100%; object-position: top;}

 .contact_form .form_col_1{margin-bottom: 30px;}
 .contact_form .form_col_1:last-child{margin-bottom: 0px;}
 .contact_form input{height: 45px; padding: 0px 20px;}
 .contact_form textarea{height: 120px; min-height: 80px; padding: 15px 20px;}
 .contact_form input,  .contact_form textarea{ background-color: black; border-radius: 6px; width: 100%; font-size: 14px; }
 input:focus,  input:active{border: none;}
 .contact_form{max-width: 500px; margin:50px auto 0px;}
 input[type="submit"]{width: fit-content; background-color: rgb(253, 224, 71); color: black; font-size: 18px; padding:10px 40px; border-radius:6px; height: auto; margin: auto; display: table; cursor: pointer; transition: 0.3s;}
 input[type="submit"]:hover{background-color: #fff; color: rgb(55, 54, 54);}

 .social_icon ul{display: flex; flex-wrap: wrap; gap: 15px; align-items: center;}
 .social_icon ul li{list-style: none; font-size: 40px;}
 .social_icon ul li a{display: inline-block; transition: 0.3s;}
 .social_icon ul li a:hover{color:  rgb(253, 224, 71);}
 header{background-color: rgb(30, 30, 30); padding: 20px 0px; transition: 0.3s;}
 .page-center{min-height: calc(100vh - 90px); display: flex; justify-content: center; flex-direction: column;}
 .active a{color: #f6e249;}
 /* .red{color: red;} */

 .menu-icon, .hide_menu{display: none;}
 


 @media only screen and (max-width: 1024px) {
    .desktopmenu ul{gap: 20px;}
    .desktopmenu ul li a{font-size: 16px;}
    .section_padding{padding: 40px 0px;}
    .mb_child_20 >*{margin-bottom: 15px;}
 p{font-size: 17px;}
 header{ padding: 15px 0px;}
 .contact_form{margin-top: 30px;}
 .contact_form .form_col_1{margin-bottom:20px;}
 input[type="submit"]{padding: 10px 40px;}



 }

 @media only screen and (max-width: 768px) {
    .desktopmenu{position: fixed;width:100vw; height: 100vh; top: 0;right: -100%; background-color: rgba(0, 0, 0, 0.7); transition: 0.3s; z-index: 10;}
    .menu_inr{position: absolute; top: 0; right: 0; width: 80%; max-width: 400px; height: 100vh; background-color: black;}
    .desktopmenu ul{flex-direction: column;margin: 100px 40px 0px;}
    .desktopmenu ul li a{font-size: 16px;}
    .desktopmenu.show_menu{right:0;}
    .menu-icon, .hide_menu{display: block;}
    .desktopmenubtn{margin-left: auto;}
    .menu-icon{position: relative; width: 25px; height: 25px; overflow: hidden;}
    .menu-icon span{position: absolute; top: 0; left: 0; width: 100%; height:3px; background-color: #fff; transition: 0.3s;}
    .menu-icon span::after, .menu-icon span::before{content: ""; height: 100%; position: absolute; right: 0; background-color:#fff; transition: 0.3s;}
    .menu-icon span::after{top: 10px; width: 50%;}
    .menu-icon span::before{top: 20px; width: 100%;}

    .hide_menu{position: absolute; right:20px; top:30px; font-size: 30px;}

    .menu-icon.make_cut span::after{transform: translateX(-40px);}
    .menu-icon.make_cut span::before{transform: rotate(90deg); top:0; }
    .menu-icon.make_cut span{transform: rotate(-47deg); top:9px;}
    .section_padding{padding: 30px 0px;}
    .mb_child_20 >*{margin-bottom: 10px;}
    header{ padding: 10px 0px;}
 .contact_form{margin-top: 20px;}
 .contact_form .form_col_1{margin-bottom: 15px;}

 input[type="submit"]{font-size: 17px; padding: 8px 40px;}
 .social_icon ul li{font-size: 30px;}

 .slick-dots li{margin-right: 6px;}
 .slick-dots li button{width: 8px; height: 8px;}
 .slick-dots{margin-top: 20px;}
.banner_img{max-width: 400px;}
.slider_col{padding: 10px;}
 }

 @media only screen and (max-width: 480px) {
   p{font-size: 15px;}
   .menu-icon span{height: 2px;}
   .skillimg{width:60px;}
   .skilltxt{width: calc(100% - 60px); padding-left: 15px;}
   input[type="submit"]{ padding: 6px 30px;}
   .slick-dots{margin-top: 15px;}
   .section_padding{padding: 25px 0px;}

   .banner_img{max-width: 400px; margin: 0px auto;}



 }